	module.exports = {"data":{"site":{"siteMetadata":{"title":"Return of the Shadow"}},"markdownRemark":{"id":"/home/kvalencik/git/rots-site/src/pages/news/001617.md absPath of file >>> MarkdownRemark","html":"<blockquote>\n<p>Message 1617 : Wed Dec 14 (Azriya)     :: The Werewolf and The Vampire</p>\n</blockquote>\n<p>Sending her thoughts across her time-forgotten domain, the lady searched\nfor her pet’s presence.  Once she located him, she felt great pain coming from\nhim.  he was always full of pain, but today felt different, somehow.  Leaving\nher tower, she flew to  his side where she found him lying, panting.  Stroking\nhis fur, she knelt and held his head in her hands.  Feeling the burn of the\naccursed jewel raging inside him, she used her ancient power to twist the\njewel’s energy into a form of healing his undead body could use.  Strengthened\nand rejuvenated, the mighty werewolf gave her an evil grin an resumed his\nperpetual guarding of their domain.</p>\n<p>OOC:  The werewolf of Dark Lake has learned new tricks.  Beware.</p>\n<p>Questions or concerns, contact Azriya (rots.staff@gmail.com).</p>","frontmatter":{"title":"News 1617: The Werewolf and The Vampire","date":"October 09, 2017"}}},"pathContext":{"path":"/news/1617"}}

